<template>
  <b-card :header="$t('Products_grp')">
    <b-button type="button" style="background-color: #ef402b;border-color: #C33421;color: white" @click="showLoader=false,clearObjects(),$refs.addProductModel.show(),defaultSelection()" class="mr-1 mb-2 mt-xl-n2 pull-right">{{$t('Add_Product_Groups')}}</b-button>

    <b-table :dark="dark" :sort-by.sync="sortBy" :hover="hover" :striped="striped"  :bordered="bordered" :small="small" :fixed="fixed" responsive="lg" :items="gridData" :fields="captions" :current-page="currentPage" :per-page="perPage">
      <template slot="top-row" slot-scope="{ fields }" v-if="currentPage==1">
        <td v-for="field in fields" :key="field.key"  >
          <input   v-model="filters[field.key]" :placeholder="' '+field.label" v-if="field.key!='actions'" >
        </td>
      </template>
      <template slot="actions" slot-scope="data">
          <a v-on:click="showLoader=false,selectObject(data.item), $refs.editProductModel.show()" v-b-tooltip.hover :title="$t('Update')" class="icon mr-2 anchor-pointer"><i id="editIcon"  class="fa font-2x2 fa-pencil"></i></a>
          <a v-on:click="selectObject(data.item), $refs.deleteProductModel.show()" v-b-tooltip.hover :title="$t('Delete')"  class="icon anchor-pointer" ><i class="cui-trash font-2x2 icons mt-1"></i></a>
      </template>
    </b-table>
    <div class="pt-1 pb-4" v-if="gridData2.length == 0">
      <!-- <b-button style="background-color: #e4e5e6" class="w-100">{{$t('No Records Found.')}}</b-button> -->
          <div class="text-center">
      <b-spinner variant="primary" label="Text Centered"></b-spinner>
          </div>
    </div>
    <nav>
      <b-pagination class="pull-right" :total-rows="totalRows" :per-page="perPage" v-model="currentPage" prev-text="Prev" next-text="Next" hide-goto-end-buttons/>
    </nav>

    <b-modal no-close-on-backdrop ref="deleteProductModel" :title="$t('Delete_Product')" class="modal-primary">
      <p>{{$t('Are_you_sure_you_want_to_delete_product')}} <b style="color: #ef402b"></b> ?</p>
      <div slot="modal-footer" class="w-100">
        <b-button type="button" v-on:click="$refs.deleteProductModel.hide(),clearObjects()" class="float-right ml-2 "><i class="fa fa-close"></i> {{$t('Cancel')}}</b-button>
        <b-button type="button" style="background-color: #ef402b;border-color: #C33421;color: white" v-on:click="deleteItem($refs.deleteProductModel,selectedProductGroup.StoreProductGroupID),clearObjects()" class="float-right ml-2 " variant="primary"><i class="fa fa-dot-circle-o"></i> {{$t('Confirm')}}</b-button>
      </div>
    </b-modal>

    <b-modal no-close-on-backdrop ref="addProductModel" size="lg" hide-footer :title="$t('Add_Product_Groups')" class="modal-primary">
      <b-form @submit.stop.prevent="showLoader=true,saveItem($refs.addProductModel, saveProductGroup)">
        <b-row>
          <b-col md="6">
            <b-form-group validated
              :label="$t('Select_store')">
              <b-input-group> 
                <b-form-select required  v-model="saveProductGroup.StoreID"  >
                  <option :value="null" disabled>{{$t('Please_select_store')}}</option>
                  <option v-for="item in storesList" :value="item.StoreID" v-bind:key="item.StoreID">{{item.StoreName}}</option>                
                </b-form-select>
              </b-input-group>
            </b-form-group>
          </b-col>
          <b-col md="6">
            <b-form-group validated
              :label="$t('Group_Name')">
              <b-form-input type="text" :placeholder="$t('Please_enter_group_name')"  v-model="saveProductGroup.GroupName"  required ></b-form-input>
            </b-form-group>
          </b-col>
        </b-row>
        <b-row>
          <b-col md="6">
             <b-form-group
              :label="$t('Profile')">
              <b-form-file required
                v-model="saveProductGroup.GroupPhotoURL"
                :state="Boolean(saveProductGroup.GroupPhotoURL)"
                :placeholder="$t('Choose_a_Grp_Profile')"
                :drop-placeholder="$t('Drop_file_here')"
              />
             </b-form-group>
          </b-col>
        </b-row>
        <hr style="margin: 20px -20px;" />
        <div slot="modal-footer" class="w-100 text-right">
          <b-button type="submit" class="ml-2"  v-if="showLoader==false" style="background-color: #ef402b;border-color: #C33421;color: white"><i class="fa fa-dot-circle-o"></i> {{$t('Save')}}
          </b-button>
          <b-button variant="primary" v-if="showLoader==true">
            <b-spinner small></b-spinner>
            <span class="sr-only">Loading...</span>
          </b-button> 
          <b-button type="button" class="ml-2" v-on:click="$refs.addProductModel.hide(),clearObjects()" ><i class="fa fa-close"></i> {{$t('Cancel')}}
          </b-button>
        </div>
      </b-form>
    </b-modal>

    <b-modal no-close-on-backdrop ref="editProductModel" size="lg" hide-footer :title="$t('Edit_Product_Groups')" class="modal-primary">
      <b-form @submit.stop.prevent="showLoader=true,updateItem($refs.editProductModel, selectedProductGroup)">
        <b-row>
          <b-col md="6">
            <b-form-group validated
              :label="$t('Select_store')">
              <b-input-group>
                <b-form-select required v-model="selectedProductGroup.StoreID">
                  <option  >All Stores</option>
                  <option v-for="item in storesList" :value="item.StoreID" v-bind:key="item.StoreID">{{item.StoreName}}</option>             
                </b-form-select>
              </b-input-group>
            </b-form-group>
          </b-col>
          <b-col md="6">
            <b-form-group validated
              :label="$t('Group_Name')">
              <b-form-input v-model="selectedProductGroup.GroupName"  type="text" :placeholder="$t('Please_enter_group_name')" required ></b-form-input>
            </b-form-group>
          </b-col>
        </b-row>
        <b-row>
             <b-col md="6">
             <b-form-group
              :label="$t('Profile')">
              <b-form-file
                v-model="selectedProductGroup.GroupPhotoURL"
                :state="Boolean(selectedProductGroup.GroupPhotoURL)"
                :placeholder="(selectedProductGroup.GroupPhotoURL ? selectedProductGroup.GroupPhotoURL : 'Choose_a_Grp_Profile')"  
                :drop-placeholder="$t('Drop_file_here')"
              />
             </b-form-group>
          </b-col>
          <b-col md="6">
             <b-form-group
              :label="$t('Profile')">
           <b-img  thumbnail :src="selectedProductGroup.GroupPhotoURL_preview"  rounded="left" style="height: 20vh; width:30vh"   alt="Upload Profile Photo"></b-img>
              </b-form-group>
          </b-col>
        </b-row>
        <hr style="margin: 20px -20px;" />
        <div slot="modal-footer" class="w-100 text-right">
          <b-button type="submit" class="ml-2" v-if="showLoader==false" style="background-color: #ef402b;border-color: #C33421;color: white"><i class="fa fa-dot-circle-o"></i>  
          {{$t('Save')}}</b-button>
          <b-button variant="primary" v-if="showLoader==true">
            <b-spinner small></b-spinner>
            <span class="sr-only">Loading...</span>
          </b-button> 
          <b-button type="button" class="ml-2" v-on:click="$refs.editProductModel.hide()" ><i class="fa fa-close"></i> {{$t('Cancel')}}</b-button>
        </div>
      </b-form>
    </b-modal>

  </b-card>
</template>

<script>
import VueNotifications from 'vue-notifications'
import * as toutPaieService from "./ToutPaieService.js";

export default {
  props: {
    caption: {
      type: String,
      default: 'Managers'
    },
    hover: {
      type: Boolean,
      default: true
    },
    striped: {
      type: Boolean,
      default: true
    },
    bordered: {
      type: Boolean,
      default: false
    },
    small: {
      type: Boolean,
      default: false
    },
    fixed: {
      type: Boolean,
      default: false
    },
    dark: {
      type: Boolean,
      default: false
    }
  },
  data: () => {
    return {
      
      selectedProductGroup: {},
      saveProductGroup: {},   
       loginUser:{},
       showLoader:false,
       editUser: false,
      storesList : [],
      orderProdctGrp: [],
      sortBy : 'StoreName',
      pageOptions: [5, 10, 15],
      currentPage: 1,
      file: null,
      buildingsList: [],
      perPage:10,
      filters: {
                StoreName: '',
                GroupName:''

              },
      productGroupList : [],
      otherAddr: [{
        storeName: null,
        city: null,
        pincode: null
      }],
      

    }
  },
  computed: {
    fields () {
      return [
        {key: 'StoreName',label: this.translateString('Store_Name'),  sortable: true},
        {key: 'GroupName',label: this.translateString('Group_Name'),  sortable: true},
        {key: 'actions' ,label: this.translateString('Action'),sortable:false,thStyle: {'width': '70px !important', 'min-width': '70px !important'}}
      ]
    },
    gridData2: function() {
      return this.productGroupList;
     
    },

    gridData () {
      const filtered = this.productGroupList.filter(item => {
        return Object.keys(this.filters).every(key =>
            String(item[key]).toLowerCase().includes( String(this.filters[key]).toLowerCase()))
      })
      return filtered.length > 0 ? filtered : [{
        StoreName: '',
      GroupName: ''
      }]
    },
    totalRows: function () {
      return this.getRowCount()
    },
    captions: function() {
      return this.fields
    }
  },
  methods: {
    translateString : function(colName) {
      return this.$i18n.t(colName);
    },
    getRowCount: function () {
      return this.productGroupList.length ;  
    },
    selectObject: function(productGroup){
 
        this.selectedProductGroup = productGroup;
       
 
         this.selectedProductGroup.GroupPhotoURL_preview=toutPaieService.ApiEndPoints+"images/"+productGroup.GroupPhotoURL;
         console.log("this.selectedProductGroup.GroupPhotoURL_preview :",this.selectedProductGroup.GroupPhotoURL_preview);
    
      //this.selectedProductGroup = JSON.stringify(selectedProductGroup); 
 
    },
    deleteItem: function (ref,id) {

      console.log('in delete function');

      let URL = 'productGroup/delete/' + id;
     
      toutPaieService.getData(URL).then(res => {
       let msg=res.status;
       if (msg=="Success") {
          
          ref.hide();
          VueNotifications.success({message:this.translateString("ProductGroup_Deleted_Successfully"),timeout:2500});
          this.clearObjects();
          this.getProductGrp();
          
        }else{
           VueNotifications.error({message:this.translateString(msg),timeout:2500});
        }

    });

    },
    updateItem: function(ref,selectedProductGroup){
      console.log('in update function');
      this.editUser = true;
      this.saveItem(ref,selectedProductGroup);

    },
    clearObjects:function(){
     this.saveProductGroup={};
     this.selectedProductGroup={};
     this.editUser = false;
     this.getProductGrp();

    },

    defaultSelection: function(){
           this.loginUser=JSON.parse(window.localStorage.getItem("loginUser"));
               if(this.loginUser.Role=="M"){
          if(this.storesList.length>0){
            for(var i=0;i<this.storesList.length;i++){

          this.saveProductGroup.StoreID=this.storesList[i].StoreID;
           }
               
          }
       }
    },
    saveItem: function(ref,saveProductGroup){
      console.log('in save itom function');

      let userData = saveProductGroup;
     
      console.log(userData);     

      var form = new FormData();
      form.append("GroupPhotoURL", userData.GroupPhotoURL);
      form.append("GroupName",userData.GroupName);
      form.append("StoreID",userData.StoreID);
       
      let url =  null;
      if (this.editUser === true && userData.StoreProductGroupID) {
        url = 'productGroup/update/' + userData.StoreProductGroupID;
        this.editUser = false;
       
      }else{
        url = 'productGroup/save';
      }
       console.log("url",url);

      toutPaieService.saveData(form, url).then(data => {
        console.log('data', data);
        let msg=data.data[0].message;
        if (data.status == 200 && msg=="Success") { 
          ref.hide();
          this.getProductGrp();
          this.clearObjects();
           this.showLoader=false;
          // msg=this.translateString("Product_Group_Successfully_Saved");
          VueNotifications.success({message: this.translateString("Product_Group_Successfully_Saved") ,timeout:2500});
        }else{
           VueNotifications.error({message:this.translateString(msg),timeout:2500});
           this.showLoader=false;
        }
      });
    },
    getProductGrp: function() {

    this.loginUser=JSON.parse(window.localStorage.getItem("loginUser"));
        if(this.loginUser.Role=="SM")
        {
           this.loginUser.SM="SM";

        }if(this.loginUser.Role=="M")
        {
          this.loginUser.M="M";
        }
        if(this.loginUser.Role=="SA")
        {
            this.loginUser.SA="SA";
        }

        let url=null;

        if(this.loginUser.Role=="M"){
         url="productGroups/user/"+this.loginUser.UserID;
             
        }else{
          url="productGroups";
        }


			toutPaieService.getData(url).then(res => {
        this.productGroupList = res.data;

			});
    },
    
    getStores: function () {
      
    
    this.loginUser=JSON.parse(window.localStorage.getItem("loginUser"));
         
        let url =null;
       if(this.loginUser.Role=="M"){
           url = 'user/store/get/'+this.loginUser.UserID;
       }else{
                     url="store";
       }
      toutPaieService.getData(url).then(res => {
        this.storesList = res.data;
        // console.log("this.storesList",this.storesList);
      });
    }

 
  },
  beforeMount(){
    this.getProductGrp();
    this.getStores();  
      
  }
}
</script>
